export default {
	data() {
		return {
			// 用户信息
			globalUser: {},
			newSex: {}
		};
	},
	onShow() {
		// 获取用户信息
		this.globalUser = uni.getStorageSync("userToken")
		this.newSex = uni.getStorageSync("newSex")
		console.log(this.globalUser)
	},
	methods: {
		// 跳转到设置头像
		operator() {
			uni.showActionSheet({
				itemList: ["查看我的头像","从相册选择上传"],
				success: (res) => {
					const index = res.tapIndex
					
					const arrImg = []
					arrImg.push(this.globalUser.faceImage || this.globalUser.avatarUrl,)
					
					if(index == 0) {
						
						// 查看我的头像
						uni.previewImage({
							urls: arrImg,
							current: arrImg[0]
						})
						
					} else {
						uni.chooseImage({
							count: 1,
							sizeType: ["compressed"],
							sourceType: ["album"],
							success: (data) => {
								// 获得图片的临时路径
								const tempFilePaths = data.tempFilePaths[0]
								
								// 选择上传头像
								uni.navigateTo({
									url: "../operator/operator?imgSrc=" + tempFilePaths
								})
							}
						})
						
					}
				}
			})
		},
		// 跳转到性别选项
		modifySex() {
			uni.navigateTo({
				url: "../sex/sex?sex=" + this.globalUser.sex
			})
		},
		// 跳转到生日选项
		modifyBirthday() {
			uni.navigateTo({
				url: "../meBirthday/meBirthday?birthday=" + this.globalUser.birthday
			})
		},
		// 跳转到昵称选项
		modifyNickname() {
			uni.navigateTo({
				url: `../meNickname/meNickname?nickname=${this.globalUser.nickName || this.globalUser.nickname}`
			})
		},
		// 点击清理缓存
		cleanStorage() {
			// 同步清理所有缓存
			uni.clearStorageSync();
			// 提示用户成功
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			})
		},
		// 点击退出登录
		logout() {
			uni.request({
				url: `${this.$api}/user/logout?userId=${this.globalUser.id}&qq=122212489`,
				method: "POST",
				success: (res) => {
					// console.log(res)
					if(res.data.status == 200) {
						// 如果服务器返回200 代表已退出成功
						// 移出指定的key
						uni.removeStorageSync("userToken")
						
						// 跳转到个人中心页面
						uni.switchTab({
							url: "../me/me"
						})
					}
				}
			})
		}
	}
}
